  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes star {
  0% {
    opacity: 1;
    transform: scale(1.3);
  }
  100% {
    opacity: 0.5;
    transform: scale(1);
  }
}

@keyframes aerospace {
  0% {
