  }
  100% {
    transform: translateY(0%);
  }
}

@keyframes ball {
  0% {
    transform: translateY(-4%);
    transform: scale(1);
  }
  100% {
    transform: translateY(0%);
    transform: scale(1.05);
  }
}

@keyframes arrow {
  0% {
    transform: translateY(10%);
  }
  100% {
    transform: translateY(-10%);
  }
}

@keyframes shake {
  0% {
    transform: scale(1);
  }
  25% {
    transform: scale(1.05);
  }
  50% {
    transform: scale(1);
  }
  75% {
    transform: scale(1.05);
  }
}

@keyframes planet {
  0% {
    transform: translateY(2%);
  }
  100% {
    transform: translateY(-2%);
  }
}

@keyframes ufo {
  0% {
    transform: translateY(-1%);
  }
  100% {
    transform: translateY(2%);
  }
}

@keyframes fadeInDown {
  0% {
    opacity: 0;
    transform: translate3d(0, 1.5rem, 0);
  }
  100% {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

.details-dialogue.dialogue {
  animation: fadeInDown 0.6s 0.5s ease both;
}

.details-scenes-list.scenes {
  animation: fadeInDown 0.6s 1s ease both;
}

.details-answer-list.answer {
  animation: fadeInDown 0.6s 1s ease both;
}

.details-nine-title {
  opacity: 1;
  transition: all 0.5s;
}

.details-nine-title.ani {
  opacity: 0;
}

.details-nine-btn {
  opacity: 1;
  transition: all 0.5s;
}

.details-nine-btn.aniBtn {
  opacity: 0;
}

@keyframes detailsAni {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.page {
  display: none !important;
}

.page.anin {
  display: block !important;
  animation: detailsAni 1.3s;
}

.details {
  height: 100%;
  overflow: scroll;
}

.wrap {
  background-color: #0b1572;
  display: none !important;
}

.wrap.show {
  display: block !important;
}

@media (min-aspect-ratio: 375 / 635) {
  .logo {
    margin: 0.2rem auto 0;
  }
  .details-dialogue {
    margin-bottom: 0.2rem;
  }
  .details-dialogue-content {
    margin-top: 1.8rem;
  }
  .details-bg {
    height: 11rem;
  }
  .details-answer-item {
    margin: 0.1rem auto 0;
  }
  .details-bottom {
    bottom: 0.1rem;
  }
  .index-title {
    margin: 0.15rem auto 0;
  }
  .details-scenes-item {
    padding: 0.05rem 0 0.15rem;
  }
}
