.details-tenth{
  // animation: fadeIn 0.5s ease-in-out 0s 1 alternate forwards;
  &-bg{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 11.9rem;
    @extend .img-item;
    background-image: url($staticUrl+'img/detail_10_bg.png');
  }
  &-ball{
    top: 3.6rem;
  }
  &-list{
    position: relative;
    margin: 0.7rem 0 0 0.5rem;
  }
  &-btn{
    width: 2.14rem;
    height: 0.52rem;
    margin-bottom: 0.1rem;
    @extend .img-item;
    background-image: url($staticUrl+'img/detail_10_btn0.png');
  }
  &-btn2{
    width: 2.14rem;
    height: 0.52rem;
    margin-bottom: 0.1rem;
    @extend .img-item;
    background-image: url($staticUrl+'img/detail_10_btn.png');
  }
  &-item{
    display: flex;
    justify-content: center;
  }
  &-text{
    font-family: "PingFang SC";
    font-size: 0.24rem;
    color: #fff;
    line-height: 0.42rem;
    white-space: pre-line;
    margin-left: 0.1rem;
  }
  &-line{
    position: relative;
    padding-top: 0.1rem;
  }
  &-circle{
    position: relative;
    width: 0.2rem;
    height: 0.2rem;
    background-color: #fff;
    border-radius: 50%;
    border: 1px solid #dd4132;
    margin-bottom: 0.24rem;
    transform: scale(0.5);
  }
  &-red{
    position: absolute;
    top: 0.16rem;
    left: 0.08rem;
    width: 0.02rem;
    height: 0.94rem;
    background-color: #dd4132;
  }
  &-list1{
    animation: fadeInDown 0.8s 0.6s ease both;
  }
  &-list2{
    animation: fadeInDown 0.8s 1.2s ease both;
  }
}
.details-dialogue-title{
  font-size: 0.5rem;
  color: #0a1571;
  padding-bottom: 0.3rem;
}